@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Roboto:ital,wght@0,100;0,400;0,900;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.button{
    /* background-color: #fff; */
}

.inverted{
    transform: rotate(180deg);
}

button{
    height: 20vh;
    width: 70vw;
    border: 10px solid black;
    border-radius: 20px;
    font-family: 'Montserrat', sans-serif;
    font-size: 4rem;
    font-weight: 900;
    box-shadow: 0 0 20px 2px rgba(0,0,0,0.5);
}
.App {
    width: 100vw;
    height: 100vh;
    /* background: url("./assets/background.gif"); */
    /* background-size: cover; */
    background-color: black;
}

.background {
    width: 100vw;
    height: 100vh;
    background-color: rgba(100, 102, 22, 0.5);
    position: absolute;
    z-index: 0;
    transition: 0.1s all ease-in-out;
}

.App__wrapper {
    height: 100vh;
    width: 70vw;
    margin: 0 auto;
    display: grid;
    place-items: center;
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.App__wrapper:first-child{
    grid-column: 1 / -1;
}

.reset{
    width: 120px;
    aspect-ratio: 1;
    background-color: black;
    color: white;
    display: grid;
    place-items: center;
    border-radius: 50%;
    font-family: 'Montserrat', sans-serif;
    font-size: 2rem;
    font-weight: 800;
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.5);
    border: 8px solid black;
}
